* {
  margin: 0;
  padding: 0;
}

.btn {
  padding: 8px 12px;
  border-radius: 7px;
  outline: none;
  border: none;
  cursor: pointer;
  margin-top: 12px;
}

.btn-primary {
  background-color: #fff;
  color: #242424;
}

.btn-outline {
  background-color: transparent;
  color: #fff;
  border: 1px solid #fff;
  transition: all 0.3s ease-out;
}

.btn-medium {
  font-size: 14px;
}

.btn-large {
  padding: 12px 26px;
  font-size: 20px;
}

.btn-medium:hover, .btn-large:hover {
  background-color: #fff;
  color: #242424;
  border: 1px solid #fff;
  transition: all 0.3s ease-out;
}

.hero-button a.btn-mobile button:hover {
  background-color: transparent;
  color: #fff;
}

.navbar {
  background: linear-gradient(90deg, rgb(28, 27, 27) 0%, rgb(26, 23, 23) 100%);
  height: 60px;
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: 1.2rem;
  position: sticky;
  top: 0;
  z-index: 999;
}

.navbar-container {
  display: flex;
  justify-content: space-around;
  align-items: center;
  height: 60px;
  width: 100%;
}

.navbar-logo {
  color: #fff;
  justify-self: start;
  margin-left: 20px;
  cursor: pointer;
  text-decoration: none;
  font-size: 2rem;
  display: flex;
  align-items: center;
}

.fa-typo3 {
  margin-left: 0.5rem;
  font-size: 1.8rem;
}

.nav-menu {
  display: grid;
  grid-template-columns: repeat(5, auto);
  grid-gap: 10px;
  list-style: none;
  text-align: center;
  width: 60vw;
  justify-content: end;
}

.nav-item {
  height: 60px;
}

.nav-links {
  color: #fff;
  display: flex;
  align-self: center;
  text-decoration: none;
  padding: 0.5rem 1rem;
  margin-top: 6px;
}

.fa-bars {
  color: #fff;
}

.nav-links-mobile {
  display: none;
}

.menu-icon {
  display: none;
}


video {
  object-fit: cover;
  width: 100%;
  height: 100vh;
  position: fixed;
  z-index: -1;
}

.hero-container {
  /* background: url('/image/image.jpg') center center/cover no-repeat; */
  width: 100%;
  height: 100vh;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  box-shadow: inset 0 0 0 1000px rgba(0,0,0,0.2);
  object-fit: contain;
}

.hero-container > h1 {
  color: #fff;
  font-size: 80px;
  margin-top: -100px;
}

.hero-container > p {
  margin-top: 8px;
  color: #fff;
  font-size: 32px;
}

.hero-button {
  margin-top: 32px;
}

.hero-button .btn {
  margin: 6px;
}

.trailer-logo {
  transform: translateY(2.5px);
  margin-left: 4px;
}

@media screen and (max-width: 960px) {
  .NavbarItems {
    position: relative;
  }

  .nav-item {
    height: 100%;
  }

  .nav-menu {
    display: flex;
    flex-direction: column;
    width: 100%;
    position: absolute;
    top: 60px;
    left: -120%;
    opacity: 1;
    font-size: 16px;
    transition: all 0.5s ease;
    padding: 0 0 1rem;
  }
  
  .nav-menu.active {
    background: #242222;
    left: 0;
    opacity: 1;
    transition: all 0.5s ease;
    z-index: 1;
  }

  .nav-links {
    text-align: center;
    width: 100%;
    display: table;
    padding: 0.5rem 0;
  }

  .nav-links:hover {
    background-color: #fff;
    color: #242424;
    border-radius: 0;
  }

  .navbar-logo {
    position: absolute;
    top: 0;
    left: 0;
    font-size: 25px;
    transform: translate(5%, 30%);
  }

  .menu-icon {
    color: #fff;
    display: block;
    position: absolute;
    top: 0;
    right: 0;
    transform: translate(-100%, 30%);
    font-size: 1.8rem;
    cursor: pointer;
  }

  .nav-item:last-child {
    width: 50%;
    display: flex;
    margin: 0 auto;
    align-content: center;
    justify-content: space-between;
  }

  .nav-links-mobile {
    display: block;
    text-align: center;
    margin: 1rem auto;
    border-radius: 4px;
    width: 30%;
    font-size: 16px;
    text-transform: uppercase;
    text-decoration: none;
    padding: 8px 12px;
    border: 1px solid #fff;
    transition: all 0.3s ease-out;
  }

  .nav-links-mobile:hover {
    background: #fff;
    color: #242424;
    transition: 250ms;
  }

  .hero-container > h1 {
    font-size: 45px;
    margin-top: -150px;
  }

  .hero-container > p {
    font-size: 20px;
  }

  .btn-mobile {
    display: block;
    text-decoration: none;
  }

  .btn {
    width: 100%;
  }
  .btn-large {
    border-radius: 0%;
    padding: 6px 12px;
    font-size: 16px;
  }
}